import React from 'react'
import WOW from 'wowjs'

import Content from './Home/Content'

export default class Home extends React.Component {

    componentDidMount() {
        let wow = new WOW.WOW({
            live: false
        })

        wow.init()
    }

    change = (social) => {
        window.open(`https://www.${social}.com/axiom18`)
    }

    render() {
        return (
            <div className="wrap-home">
                <Content />

                <div className="something wow fadeIn" data-wow-delay="3s" data-wow-duration=".5s" >
    
                    <img src={require('../css/logo.jpg')} data-wow-offset="100" data-wow-delay="3.5s" className="logo wow slideInDown" alt="LOGO"/>

                    <h1 className="follow-text wow fadeIn" data-wow-delay="3.5s" >Make sure you are following us</h1>
    
                    <div className="social-media">
                        <img src={require('../css/instagram.svg')} className="igIM wow bounceInUp" alt="IG" onClick={()=>this.change('instagram')}
                            data-wow-offset="100" data-wow-duration=".75s" data-wow-delay="3.75s"/>
                        <img src={require('../css/facebook.svg')} className="igIM wow bounceInUp" alt="FB" onClick={() => this.change('facebook')}
                            data-wow-offset="100" data-wow-duration=".75s" data-wow-delay="3.75s"/>
                    </div>

                    <h1 className="team-axiom wow slideInRight" data-wow-offset="10" data-wow-delay="4s" >- Team AXIOM</h1>
                </div>


            </div>
        )
    }
}



