import React from 'react'
import WOW from 'wowjs'

export default class Content extends React.Component {

    componentDidMount() {
        let wow = new WOW.WOW({
            live: false
        })

        wow.init()
    }

    render() {
        return (
            <div className="wrap-content">
                <h1 className="we-are-coming wow fadeIn" data-wow-delay="1s" data-wow-duration="1s" data-wow-offset="200" >WE ARE CoMING.</h1>
            </div>
        )
    }
}




